import {paxios} from '../../../utilities/axios';

export const login = async (corr, contra)=>{
  try
  {
    const {data} = await paxios.post(
      "/api/Seguridad/login",
      {
        correo: corr,
        contraseña: contra
      }
    );
    return data;
  }catch (e){
    throw(e);
  }
}
